<template>
    <div class="wrapper">
        <img class="wrapper__img" src="http://www.dell-lee.com/imgs/vue3/user.png" />
        <div class="wrapper__input">
            <input
             class="wrapper__input__content"
             placeholder="请输入用户名"
             v-model="username"
            />
        </div>
        <div class="wrapper__input">
            <input
             class="wrapper__input__content"
             placeholder="请输入密码"
             type="password"
             v-model="password"
            />
        </div>
        <div class="wrapper__input">
            <input
             class="wrapper__input__content"
             placeholder="确认密码"
             type="password"
             v-model="ensurement"
            />
        </div>
        <div class="wrapper__register-button" @click="handleRegister">注册</div>
        <div class="wrapper__register-link" @click="handleLoginClick">已有账号去登陆</div>
    </div>
    <Toast v-if="show" :message="toastMessage" />
</template>

<script>
import { reactive, toRefs } from 'vue'
import { useRouter } from 'vue-router'
import { post } from '../../utils/request'
import Toast, { useToastEffect } from '../../components/Toast'

// 处理注册逻辑
const useRegisterEffect = (showToast) => {
  const router = useRouter()
  const data = reactive({
    username: '',
    password: '',
    ensurement: ''
  })
  const handleRegister = async () => {
    try {
      const result = await post('/api/user/register', {
        username: data.username,
        password: data.password
      })
      if (result?.errno === 0) {
        const regUsername = /^[a-zA-Z]{1}([a-zA-Z0-9]|[._]){4,19}$/
        const regPassword = /^(\w){6,20}$/
        if (data.username.length === 0) {
          showToast('用户名不能为空')
        } else if (data.password.length === 0) {
          showToast('密码不能为空')
        } else if (data.ensurement.length === 0) {
          showToast('请填写确认密码')
        } else if (data.ensurement !== data.password) {
          showToast('两次密码输入不一致')
        } else if (!regUsername.test(data.username)) {
          showToast('用户名：只能输入5-20个以字母开头、可带数字、“_”、“.”的字串')
        } else if (!regPassword.test(data.password)) {
          showToast('密码：只能输入6-20个字母、数字、下划线')
        } else {
          router.push({ name: 'Login' })//   进行路由跳转
        }
      } else {
        showToast('用户注册失败')
      }
    } catch (e) { //  请求的地址没有返回任何结果（请求失败），就会执行catch操作
      showToast('网络请求失败')
    }
  }
  const { username, password, ensurement } = toRefs(data)
  return { username, password, ensurement, handleRegister }
}

// 处理登录跳转
const useLoginEffect = () => {
  const router = useRouter()
  const handleLoginClick = () => {
    router.push({ name: 'Login' })
  }
  return { handleLoginClick }
}

export default {
  name: 'Register',
  components: { Toast },
  setup () {
    const { show, toastMessage, showToast } = useToastEffect()
    const { username, password, ensurement, handleRegister } = useRegisterEffect(showToast)
    const { handleLoginClick } = useLoginEffect()

    return { show, toastMessage, username, password, ensurement, handleRegister, handleLoginClick }
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/viriables.scss';
.wrapper {
    position: absolute;
    padding: 0 .4rem;
    top: 50%;
    left: 0;
    right: 0;
    transform: translateY(-50%);
    &__img {
        display: block;
        height: .66rem;
        width: .66rem;
        margin: 0 auto .4rem auto;
    }
    &__input {
        height: .48rem;
        margin-bottom: .16rem;
        padding: 0 .16rem;
        border: .01rem solid rgba(0,0,0,0.10);
        border-radius: .06rem;
        border-radius: .06rem;
        background-color: #F9F9F9;
        &__content {
            width: 100%;
            line-height: .48rem;
            border: none;
            outline: none;
            background: none;
            font-size: .16rem;
            color: $content-notice-fontcolor;
            &::placeholder {
                color: $content-notice-fontcolor;
            }
        }
    }
    &__register-button {
        margin-top: .32rem;
        line-height: .48rem;
        color: $btn-bgColor;
        box-shadow: 0 .04rem .08rem 0 rgba(0,145,255,0.32);
        background-color: $btn-bgColor;
        font-size: .16rem;
        color: $bgColor;
        text-align: center;
        border-radius: .04rem;
        border-radius: .04rem;
    }
    &__register-link {
        margin-top: .16rem;
        line-height: .2rem;
        font-size: .14rem;
        color: $content-notice-fontcolor;
        text-align: center;
    }
}
</style>
